@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600& display=swap");

*{
  box-sizing: border-box;
}

body{
  background-color: rgb(225, 238, 225);
  color: white;
  font-family: "Poppins", sans-serif;
  margin: 0;
}
.root{
  display: flex;
  flex-direction: column;
}

header{
  background-color: #373b69;
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}


.search{
  background-color: transparent;
  border: 2px solid #22254b;
  border-radius: 50px;
  color: white;
  font-family: inherit;
  font-size: 1.2rem;
  padding: 0.5rem 1.5rem;
}

.search:focus{
  outline: 0;
}

.movie-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movie{
  background-color: #373b69;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
  margin: 1rem;
  width: 250px;
}

.movie img{
  object-fit: cover;
  height: 450px;
  width: 100%;
}

.movie-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.movie-info h3{
  margin: 0;
}

.movie-over{
  background-color: #fff;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.1);
  color: #22254b;
  padding: 1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);/*pushed down 100%*/
  transition: transform 0.3s ease-in-out; 
}

.movie:hover .movie-over{
  transform: translateY(0%);/*appear*/
}

.tag{
  background-color: #22254b;
  border-radius: 5px;
  padding: 0.5rem;
  font-weight: bold;
}

.tag.green {
  color: rgb(5, 184, 5);
}
.tag.orange{
  color: orange;
}
.tag.red{
  color: red;
}